.project {
  width: 100%;
  padding: 2em;
  margin: 0 auto;
  text-align: center;
  box-shadow: var(--shadow);
  transition: transform 0.2s linear;
  /* transition: color 2s, background-color 2s, transform 1s linear; */
}

.project h3 {
  color: var(--clr-primary);
}

.project:hover {
  transform: translateY(-7px);
  background-color: rgb(241, 240, 240);
  /* transform:rotatey(360deg); */

}

.project__description {
  margin-top: 1em;
}

.project__stack {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1.2em 0;
}

.project__stack-item {
  margin: 0.5em;
  font-weight: 500;
  font-size: 0.8rem;
  color: var(--clr-fg-alt);
}

.project .link--icon {
  margin-left: 0.5em;
}

