* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  background-color: inherit;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  box-shadow: none;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  width:100% ;
}
#root{
  width: 100%;
}
h1,
h2,
h3,
h4 {
  line-height: 1.2;
  color: var(--clr-fg-alt);
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.3rem;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}


@media screen and (max-width: 900px) {
  h1 {
    font-size: 2.6rem;
  }

  h2 {
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 400px) {
  h1 {
    font-size: 1.7rem;
  }

  h2 {
    font-size: 1.4rem;
  }

}

@media screen and (max-width: 350px) {
  h1 {
    font-size: 1.3rem;
  }

  h2 {
    font-size: 1.1rem;
  }

}



@media screen and (max-width: 600px) {
  #root{
    width: 100%;
  }
}

@media screen and (max-width: 300px) {
  #root{
    width: 100%;
  }
}


  
